  --sds_theme_color_yellow_tint_1: #{sds-color(yellow, 90)};
  --sds_theme_color_yellow_tint_2: #{sds-color(yellow, 80)};

  --sds_theme_color_red: #{sds-color(red, 40)};
  --sds_theme_color_red_interactive: #{sds-color(red, 30)};
  --sds_theme_color_red_tint_1: #{sds-color(red, 90)};
  --sds_theme_color_red_tint_2: #{sds-color(red, 80)};

  --sds_theme_color_indigo: #{sds-color(indigo, 40)};
