  --sds_theme_color_indigo_tint_2: #{sds-color(indigo, 80)};

  --sds_theme_color_purple: #{sds-color(purple, 40)};
  --sds_theme_color_purple_interactive: #{sds-color(purple, 30)};
  --sds_theme_color_purple_tint_1: #{sds-color(purple, 90)};
  --sds_theme_color_purple_tint_2: #{sds-color(purple, 80)};

  --sds_theme_color_lime: #{sds-color(lime, 40)};
  --sds_theme_color_lime_interactive: #{sds-color(lime, 30)};
  --sds_theme_color_lime_tint_1: #{sds-color(lime, 90)};
  --sds_theme_color_lime_tint_2: #{sds-color(lime, 80)};

  --sds_theme_color_teal: #{sds-color(teal, 40)};
  --sds_theme_color_teal_interactive: #{sds-color(teal, 30)};
  --sds_theme_color_teal_tint_1: #{sds-color(teal, 90)};
  --sds_theme_color_teal_tint_2: #{sds-color(teal, 80)};

  --sds_theme_color_orange: #{sds-color(orange, 40)};
  --sds_theme_color_orange_interactive: #{sds-color(orange, 30)};
  --sds_theme_color_orange_tint_1: #{sds-color(orange, 90)};
  --sds_theme_color_orange_tint_2: #{sds-color(orange, 80)};

  --sds_theme_color_pink: #{sds-color(pink, 40)};
  --sds_theme_color_pink_interactive: #{sds-color(pink, 30)};
  --sds_theme_color_pink_tint_1: #{sds-color(pink, 90)};
  --sds_theme_color_pink_tint_2: #{sds-color(pink, 80)};

  // ------------------------------
  // Semantic Colors
  // ------------------------------
  --sds_theme_color_primary: var(--sds_theme_color_blue);
  --sds_theme_color_primary_interactive: var(--sds_theme_color_blue_interactive);
  --sds_theme_color_primary_tint_1: var(--sds_theme_color_blue_tint_1);
