  --sds_theme_color_success_tint_1: var(--sds_theme_color_green_tint_1);
  --sds_theme_color_success_tint_2: var(--sds_theme_color_green_tint_2);

  --sds_theme_color_warning: var(--sds_theme_color_yellow);
  --sds_theme_color_warning_interactive: var(--sds_theme_color_yellow_interactive);
  --sds_theme_color_warning_tint_1: var(--sds_theme_color_yellow_tint_1);
  --sds_theme_color_warning_tint_2: var(--sds_theme_color_yellow_tint_2);

  --sds_theme_color_danger: var(--sds_theme_color_red);
  --sds_theme_color_danger_interactive: var(--sds_theme_color_red_interactive);
  --sds_theme_color_danger_tint_1: var(--sds_theme_color_red_tint_1);
  --sds_theme_color_danger_tint_2: var(--sds_theme_color_red_tint_2);

  // ------------------------------
  // Backgrounds
  // ------------------------------
  --sds_theme_background: #{sds-color(grey, 90)};
  --sds_theme_background_high: #{sds-color(grey, 80)};
  --sds_theme_background_low: #{sds-color(grey, 100)};
  --sds_theme_background_contrast: #{sds-color(white)};
  --sds_theme_background_contrast_2: #{sds-color(grey, 05)};
  --sds_theme_background_gradient: linear-gradient(
    to bottom,
    var(--sds_theme_background_high) 0%,
    var(--sds_theme_background) 100%
  );

  // ------------------------------
  // Text
  // ------------------------------
  --sds_theme_text: #{sds-color(white)};
  --sds_theme_text_muted: #{sds-color(grey, 40)};
  --sds_theme_text_contrast: #{sds-color(grey, 100)};
  --sds_theme_text_contrast_muted: #{sds-color(grey, 70)};
  --sds_theme_text_highlighted: var(--sds_theme_text);
  --sds_theme_text_highlighted_background: var(--sds_theme_color_primary_tint_2);

  // ------------------------------
  // Borders
  // ------------------------------
  --sds_theme_border: #{sds-color(grey, 60)};
  --sds_theme_border_contrast: #{sds-color(grey, 30)};

  // ------------------------------
  // Icons
  // ------------------------------
  --sds_theme_icon: #{sds-color(grey, 20)};
  --sds_theme_icon_muted: #{sds-color(grey)};
  --sds_theme_icon_dark: #{sds-color(grey, 100)};
  --sds_theme_icon_light: #{sds-color(white)};

  // ------------------------------
  // Buttons
  // ------------------------------
  --sds_theme_button_background: var(--sds_theme_background_gradient);
  --sds_theme_button_border: var(--sds_theme_border);
  --sds_theme_button_text: var(--sds_theme_text);
  --sds_theme_button_icon: var(--sds_theme_icon);
  --sds_theme_button_interactive_background: var(--sds_theme_background_low);
  --sds_theme_button_interactive_border: var(--sds_theme_color_neutral);
  --sds_theme_button_interactive_text: var(--sds_theme_button_text);
  --sds_theme_button_interactive_icon: var(--sds_theme_icon_light);

  --sds_theme_button_primary_background: var(--sds_theme_color_primary);
  --sds_theme_button_primary_border: var(--sds_theme_color_primary);
  --sds_theme_button_primary_text: var(--sds_theme_text_contrast);
  --sds_theme_button_primary_icon: var(--sds_theme_icon_dark);
  --sds_theme_button_primary_interactive_background: var(--sds_theme_color_primary_interactive);
  --sds_theme_button_primary_interactive_border: var(--sds_theme_color_primary_interactive);
  --sds_theme_button_primary_interactive_text: var(--sds_theme_button_primary_text);
  --sds_theme_button_primary_interactive_icon: var(--sds_theme_button_primary_icon);

  --sds_theme_button_secondary_background: var(--sds_theme_button_background);
  --sds_theme_button_secondary_border: var(--sds_theme_button_border);
  --sds_theme_button_secondary_text: var(--sds_theme_color_primary_interactive);
  --sds_theme_button_secondary_icon: var(--sds_theme_color_primary);
  --sds_theme_button_secondary_interactive_background: var(--sds_theme_button_primary_interactive_background);
  --sds_theme_button_secondary_interactive_border: var(--sds_theme_button_primary_interactive_border);
  --sds_theme_button_secondary_interactive_text: var(--sds_theme_button_primary_interactive_text);
  --sds_theme_button_secondary_interactive_icon: var(--sds_theme_button_primary_interactive_icon);

  --sds_theme_button_ghost_background: transparent;
  --sds_theme_button_ghost_text: var(--sds_theme_button_text);
  --sds_theme_button_ghost_icon: var(--sds_theme_button_icon);
  --sds_theme_button_ghost_interactive_background: var(--sds_theme_color_neutral_tint_2);
  --sds_theme_button_ghost_interactive_text: var(--sds_theme_button_ghost_text);
  --sds_theme_button_ghost_interactive_icon: var(--sds_theme_button_interactive_icon);

  --sds_theme_button_danger_background: var(--sds_theme_color_danger);
  --sds_theme_button_danger_border: var(--sds_theme_color_danger_interactive);
  --sds_theme_button_danger_text: var(--sds_theme_text_contrast);
  --sds_theme_button_danger_icon: var(--sds_theme_icon_light);
  --sds_theme_button_danger_interactive_background: var(--sds_theme_color_danger_interactive);
  --sds_theme_button_danger_interactive_border: var(--sds_theme_button_danger_border);
  --sds_theme_button_danger_interactive_text: var(--sds_theme_button_danger_text);
  --sds_theme_button_danger_interactive_icon: var(--sds_theme_button_danger_icon);

  --sds_theme_button_danger_alt_background: var(--sds_theme_button_background);
  --sds_theme_button_danger_alt_border: var(--sds_theme_button_border);
  --sds_theme_button_danger_alt_text: var(--sds_theme_color_danger_interactive);
  --sds_theme_button_danger_alt_icon: var(--sds_theme_color_danger);
  --sds_theme_button_danger_alt_interactive_background: var(--sds_theme_button_danger_interactive_background);
  --sds_theme_button_danger_alt_interactive_border: var(--sds_theme_button_danger_interactive_border);
  --sds_theme_button_danger_alt_interactive_text: var(--sds_theme_button_danger_interactive_text);
  --sds_theme_button_danger_alt_interactive_icon: var(--sds_theme_button_danger_interactive_icon);

  // ------------------------------
  // Controls
  // ------------------------------
  --sds_theme_control_background: var(--sds_theme_background_high);
  --sds_theme_control_border: var(--sds_theme_border);
  --sds_theme_control_text: var(--sds_theme_text);
  --sds_theme_control_icon: var(--sds_theme_icon_muted);
  --sds_theme_control_placeholder: var(--sds_theme_text_muted);

  --sds_theme_control_hover_background: var(--sds_theme_control_background);
  --sds_theme_control_hover_border: var(--sds_theme_color_neutral);

  --sds_theme_control_focus_background: var(--sds_theme_background_low);
  --sds_theme_control_focus_border: var(--sds_theme_control_hover_border);

  --sds_theme_control_disabled_background: var(--sds_theme_control_background);
  --sds_theme_control_disabled_border: var(--sds_theme_control_border);
  --sds_theme_control_disabled_text: var(--sds_theme_control_text);
  --sds_theme_control_disabled_icon: var(--sds_theme_control_icon);
  --sds_theme_control_disabled_placeholder: var(--sds_theme_control_placeholder);

  --sds_theme_control_error_background: var(--sds_theme_color_danger_tint_1);
  --sds_theme_control_error_border: var(--sds_theme_color_danger);
  --sds_theme_control_error_text: var(--sds_theme_color_danger_interactive);
  --sds_theme_control_error_icon: var(--sds_theme_color_danger);

  // ******************************
  // Comm.land Suppport
  // ******************************

  // Toolbar **********************
  --comm_toolbar_background: #{sds-color(grey, 90)};
  --comm_toolbar_border: #{sds-color(grey, 60)};
  --comm_toolbar_text: #{sds-color(white)};

  --comm_toolbar_action_background: var(--comm_toolbar_background);
  --comm_toolbar_action_icon: #{sds-color(grey, 40)};
  --comm_toolbar_action_interactive_background: #{sds-color(grey, 100)};
  --comm_toolbar_action_interactive_icon: #{sds-color(white)};

  --comm_toolbar_masq_background: var(--sds_theme_color_warning);
  --comm_toolbar_masq_avatar_background: #{sds-color(grey, 100)};
  --comm_toolbar_masq_avatar_icon: var(--comm_toolbar_masq_background);
  --comm_toolbar_masq_text: #{sds-color(grey, 100)};
  --comm_toolbar_masq_interactive_background: var(--sds_theme_color_warning_interactive);

  // Dock *************************
  --comm_dock_background: #{sds-color(grey, 90)};
  --comm_dock_border: #{sds-color(grey, 60)};

  --comm_dock_menuButton_background: var(--comm_dock_background);
  --comm_dock_menuButton_border: var(--comm_dock_border);
  --comm_dock_menuButton_icon: #{sds-color(white)};
  --comm_dock_menuButton_interactive_background: var(--sds_theme_color_primary);
  --comm_dock_menuButton_interactive_icon: var(--comm_dock_menuButton_icon);

  --comm_dock_indicator_color: #{sds-color(white)};
  --comm_dock_indicator_outline: 0 0 0 2px var(--comm_dock_background), 0 0 0 4px var(--comm_dock_indicator_color);

  // Dock Menu ********************
  --comm_dockMenu_background: #{sds-color(grey, 90)};
  --comm_dockMenu_border: #{sds-color(grey, 100)};

  --comm_dockMenu_tab_background: #{sds-color(grey, 100)};
  --comm_dockMenu_tab_border: #{sds-color(grey, 60)};
  --comm_dockMenu_tab_text: #{sds-color(grey, 40)};
  --comm_dockMenu_tab_interactive_background: var(--comm_dockMenu_background);
  --comm_dockMenu_tab_interactive_text: #{sds-color(white)};
  --comm_dockMenu_tab_active_highlight: var(--sds_theme_color_primary);

  --comm_dockMenu_control_background: var(--comm_dockMenu_background);
  --comm_dockMenu_control_border_color: #{sds-color(grey, 60)}; 
  --comm_dockMenu_control_icon: #{sds-color(grey)};
  --comm_dockMenu_control_placeholder: #{sds-color(grey, 40)};
  --comm_dockMenu_control_text: #{sds-color(white)};
  --comm_dockMenu_control_hover_background: var(--comm_dockMenu_control_background);
  --comm_dockMenu_control_hover_border_color: #{sds-color(grey)};
  --comm_dockMenu_control_focus_background: #{sds-color(grey, 100)};

  --comm_dockMenu_button_background: linear-gradient(
    to bottom, 
    #{sds-color(grey, 90)} 0%, 
    #{sds-color(grey, 100)} 100%
  );
  --comm_dockMenu_button_border: #{sds-color(grey, 60)};
  --comm_dockMenu_button_icon: #{sds-color(grey, 40)};
  --comm_dockMenu_button_text: #{sds-color(white)};
  --comm_dockMenu_button_interactive_background: #{sds-color(grey, 100)};
  --comm_dockMenu_button_interactive_border: #{sds-color(grey)};
  --comm_dockMenu_button_interactive_icon: #{sds-color(white)};
  --comm_dockMenu_button_interactive_text: var(--comm_dockMenu_button_text);

  --comm_dockMenu_listItem_background: var(--comm_dockMenu_background);
  --comm_dockMenu_listItem_text: #{sds-color(grey, 40)};
  --comm_dockMenu_listItem_interactive_background: #{sds-color(grey, 100)};
  --comm_dockMenu_listItem_interactive_text: #{sds-color(white)};;
  --comm_dockMenu_listItem_button_icon: #{sds-color(grey, 40)};
  --comm_dockMenu_listItem_button_interactive_background: #{sds-color(grey, 20)};
  --comm_dockMenu_listItem_button_interactive_icon: #{sds-color(grey, 100)};
  --comm_dockMenu_listItem_button_active_icon: var(--sds_theme_color_primary);

  --comm_dockMenu_tooltip_background: #{sds-color(white)};
  --comm_dockMenu_tooltip_text: #{sds-color(grey, 100)};

  --comm_dockMenu_badge_background: #{sds-color(grey, 80)};
  --comm_dockMenu_badge_text: #{sds-color(white)};
}
