.iconList {
  padding-top: 84px;
  padding-bottom: 80px;
}

.iconList .searchBar {
  width: 100%;
  z-index: 90;
  background: var(--sds_theme_background);
  border-bottom: 1px solid var(--sds_theme_border);
  margin-bottom: 1.5rem;
  padding: 1rem 0;
  position: sticky;
  top: 84px;
  left: 0;
}

.iconList .searchBar .container {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.iconList .searchBar .container h2 {
  color: var(--sds_theme_text);
  margin-right: 1rem;
  font-size: 2rem;
  line-height: 2.75rem;
}

.iconList .searchBar .container .sds_Search {
  width: 100%;
  max-width: 500px;
}

@media screen and (max-width: 1023px) {
  .iconList .searchBar .container h2 {
    flex-shrink: 0;
    font-size: 1.25rem;
    font-weight: 600;
    line-height: 1.75rem;
  }
}

@media screen and (max-width: 767px) {
  .iconList .searchBar .container h2 {
    font-size: 1rem;
    font-weight: 600;
    line-height: 1.5rem;
  }
}

.iconList .container {
  width: 100%;
  max-width: 1440px;
  margin: auto;
  padding: 0 1rem;
}

.iconList .container .list {
  grid-gap: 1.5rem;
  grid-template-columns: repeat(4, 1fr);
  padding-bottom: 4rem;
  display: grid;
}

@media screen and (max-width: 1023px) {
  .iconList .container .list {
    grid-gap: 1.5rem;
    grid-template-columns: repeat(3, 1fr);
    display: grid;
  }
}

@media screen and (max-width: 767px) {
  .iconList .container .list {
    grid-gap: .75rem;
    grid-template-columns: repeat(2, 1fr);
    display: grid;
  }
}

@media screen and (max-width: 599px) {
  .iconList .container .list {
    grid-gap: .75rem;
    grid-template-columns: repeat(1, 1fr);
    display: grid;
  }
}

.iconList .container .list .card {
  border: 1px solid var(--sds_theme_border);
  background: var(--sds_theme_background_high);
  cursor: pointer;
  border-radius: .25rem;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem;
  transition: all .25s ease-in-out;
  display: flex;
}

.iconList .container .list .card .sds_Telicon, .iconList .container .list .card span {
  pointer-events: none;
  color: var(--sds_theme_text);
}

.iconList .container .list .card:hover {
  border-color: var(--sds_theme_color_primary);
  transform: scale(1.05);
  box-shadow: 0 4px 16px #0000003d;
}

.iconList .container .list .card:hover span, .iconList .container .list .card:hover .sds_Telicon {
  color: var(--sds_theme_color_primary);
}

.iconList .container .list .card:active {
  transform: scale(1.08);
}

.iconList .container .list .card .sds_Telicon {
  margin-right: .75rem;
}

